import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmFullscreenControl,
  VmControl,
  VmIcon,
  VmTooltip 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-fullscreen-control', VmFullscreenControl);
define('vm-control', VmControl);
define('vm-icon', VmIcon);
define('vm-tooltip', VmTooltip);
  

export interface FullscreenControlProps extends JSX.VmFullscreenControl, HTMLAttributes<HTMLVmFullscreenControlElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmFullscreenControlElement, FullscreenControlProps>(
  'vm-fullscreen-control',
  new Set(['enterIcon','exitIcon','icons','tooltipPosition','tooltipDirection','hideTooltip','keys','isFullscreenActive','i18n','playbackReady']),
);
  